/* Add padding to containers */
.register-container {
    display:none;
    position: fixed;
    top: 30%;
    right:100px;
    z-index: 1;
  }
  /* Full-width input fields */
  input[type=text], input[type=password] {
    width: 200px;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: block;
    border: none;
    background: #c8ccd1;
  }
  
  input[type=text]:focus, input[type=password]:focus {
    background-color: #ddd;
    outline: none;
  }
  
  /* Overwrite default styles of hr */
  hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
  }
  
  /* Set a style for the submit button */
  .registerbtn {
    background-color: #d18040;
    color: white;
    padding: 16px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
  }
  
  .registerbtn:hover {
    opacity: 1;
  }