@import url(https://fonts.googleapis.com/css?family=Lato:300|Montserrat:700,400);
body {
  margin: 0;
  padding: 0;
}
html {
  height:100%;
}
@font-face {
  font-family: 'Source Sans Pro';
  src: url('https://fonts.googleapis.com/css?family=Source+Sans+Pro');
  font-weight: normal;
  font-style: normal;

}

.App {
  text-align: center;
  overflow: hidden;
}
.round {
  position: absolute;
  width: 40px;
  border-radius: 50%;
  font-size: 30px;
  background-color: #f1f1f1;
  color: #242124;
}
.round:hover {
  background-color: #ddd;
  color: #242124;
}
.left {
  top: 40%;
  left: 0px;
}
.right {
  top: 40%;
  right: 10px;
}
.gallery-container {
  position: fixed;
}
.content {
  position: absolute;
  top: 100vh;
  width: 100%;
  background-color: white;
}
.text-overlay {
  position: fixed;
  width: 100%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
div > h2 {
  color: white;
  padding-top: 120px;
  font-size: 60px;
}
.text-overlay > h3 {
  color: white;
  font-size: 40px;
}
.blinking-cursor {
  font-weight: 200;
  font-size: 40px;
  color: white;
  -webkit-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite;
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}
header {
    position: fixed;
    height: 80px;
    width: 100%;
    top: 0;
    -webkit-transition: top 0.2s ease-in-out;
    transition: top 0.2s ease-in-out;
    z-index: 1;
    background-color: rgba(12, 9, 4, 0.9);
}
.nav-up {
    top: -80px;
}
header > img {
    padding-left: 50px;
    width: 80px;
    height: 80px;
    float: left;
}
header > a {
    position: absolute;
    right: 100px;
    top: 10px;
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    margin: 10px;
}

.menu-container {
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 1;
}
.hidden {
    display:none;
}
.show {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}
div > a {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    -webkit-transition: opacity 1s, -webkit-transform 0.5s;
    transition: opacity 1s, -webkit-transform 0.5s;
    transition: transform 0.5s, opacity 1s;
    transition: transform 0.5s, opacity 1s, -webkit-transform 0.5s;
    opacity: 0;
    color: #20242F;
    display: block;
    font-size: 40px;
    text-decoration: none;
    font-weight: bold;
    padding: 10px;
}
.link-container {
    position: fixed;
    top: 30%;
    right:100px;
    z-index: 1;
}
/* Icon 1 */

#nav-icon1 {
    width: 60px;
    height: 50px;
    position: fixed;
    right: 20px;
    top: 20px;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    cursor: pointer;
    z-index: 2;
}
#nav-icon1:hover {
  -webkit-transform: scale(1.3, 1.3);
  transform: scale(1.3, 1.3);
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}
  
  #nav-icon1 span{
    display: block;
    position: absolute;
    height: 5px;
    width: 100%;
    background: #fff;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
  }
  
  #nav-icon1 span:nth-child(1) {
    top: 0px;
  }
  
  #nav-icon1 span:nth-child(2) {
    top: 12px;
  }
  
  #nav-icon1 span:nth-child(3) {
    top: 25px;
  }
  
  #nav-icon1.open span:nth-child(1) {
    top: 12px;
    background: #000;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }
  
  #nav-icon1.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  
  #nav-icon1.open span:nth-child(3) {
    top: 13px;
    background: #000;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }
/* Add padding to containers */
.register-container {
    display:none;
    position: fixed;
    top: 30%;
    right:100px;
    z-index: 1;
  }
  /* Full-width input fields */
  input[type=text], input[type=password] {
    width: 200px;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: block;
    border: none;
    background: #c8ccd1;
  }
  
  input[type=text]:focus, input[type=password]:focus {
    background-color: #ddd;
    outline: none;
  }
  
  /* Overwrite default styles of hr */
  hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
  }
  
  /* Set a style for the submit button */
  .registerbtn {
    background-color: #d18040;
    color: white;
    padding: 16px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
  }
  
  .registerbtn:hover {
    opacity: 1;
  }
.text {
  font-family: 'Muli', sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  list-style: none;
  border-bottom: 0;
}

.text.hidden {
  border-bottom: 1px solid #fff;
}

.text li {
  display: inline-block;
  float: left;
  font-weight: 700;
  font-size: 2em;
  color: #fff;
  opacity: 1;
  -webkit-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
  max-width: 2em;
}
.text.hidden li.spaced {
  padding-left: 0;
}
.text li.spaced {
  padding-left: 0.5em;
}

.text.hidden li.ghost {
  opacity: 0;
  max-width: 0;
}

h4 {
  width: 200px;
}

h1 {
  font-size: 32px;
  text-decoration: none;
  font-weight: normal;
}
img {
    width: 100px;
    height: auto;
}
.item {
    vertical-align: top;
    display: inline-block;
    text-align: center;
    margin: 50px;
}
